package widgets

import constants.Constants
import game.Game
import game.GuessResult
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.button
import kotlinx.html.dom.append
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLElement
import toPx

class StatisticsPage {
    var returnButtonFunction = {}
    var shareStatisticsFunction = {}

    fun generate(width: Double, topHeightToWidth: Double): HTMLDivElement {
        val topPartHeight = width*topHeightToWidth
        return document.create.div {
            id ="howToDiv"

        }.also {
            it.append(generateTopPart(topPartHeight))

            it.append(generateLastGame(topPartHeight))

            it.append(generateAllGames(topPartHeight))
        }
    }


    fun generateLastGame(topPartHeight: Double): HTMLDivElement {
        return document.create.div {
            this.id = "lastGameStats"
            style = """
                font-family: openSans, sans-serif;
                font-size: ${toPx(topPartHeight * 0.3)};
                width: 90%
                text-align: center;
                margin-top: ${toPx(topPartHeight*0.3)};
                margin-bottom: ${toPx(topPartHeight * 0.2)};
            """
        }.also {
            it.hidden = true
            it.append{
                div { // results
                    id = "lastResultHead"
                    style="""
                        width: 100%;
                        text-align: center;
                        font-family: guessFont, sans-serif;
                        font-size: ${toPx(topPartHeight * 0.6)};
                    """
                    text("placeholder head")
                }

                div { // results
                    id = "lastResult"
                    style="""
                        width: 100%;
                        text-align: center;
                        margin-bottom: ${toPx(topPartHeight * 0.2)};
                    """
                    text("placeholder result")
                }

                div { // time head
                    style="""
                        width: 100%;
                        text-align: center;
                    """
                    text("Oyun Süresi")
                }

                div{
                    id = "lastTimer"
                    style="""
                        width: 100%;
                        text-align: center;
                        font-size: ${toPx(topPartHeight * 0.6)};
                        margin-bottom: ${toPx(topPartHeight * 0.2)};
                        font-family: guessFont, sans-serif;
                    """
                    text("00:00")
                }

                div { // word
                    id = "lastWord"
                    style="""
                        width: 100%;
                        text-align: center;
                        font-family: guessFont, sans-serif;
                        font-size: ${toPx(topPartHeight * 0.6)};
                    """
                    text("SİMİT")

                }

                div { // word
                    id = "lastDict"
                    style="""
                        width: 100%;
                        text-align: center;
                        
                    """
                    text("")

                }

                // dictionary can come here
            }
        }
    }

    private fun addButton(height: Double, buttonText: String, clickFunc: ()->Unit={}): HTMLDivElement {
        val buttonHole = document.create.div{
            id="challenge div"
            style =
                """
                    width: 100%;
                    opacity: 1;
                """
        }
        document.create.button{
            text(buttonText)
            onClickFunction = {clickFunc()}
            style="""
                padding: 0;
                width:  90%;
                height: ${toPx(height * 0.6)};
                margin-top: ${toPx(height * 0.1)};
                margin-bottom: ${toPx(height * 0.1)};
                margin-left: 5%;
                margin-right: 5%;
                font-family: openSans, sans-serif;
                font-size: ${toPx(height * 0.3)};
            """.trimIndent()
        }.also {
            it.className = "buttonClass"
            buttonHole.append(it)
        }
        return buttonHole
    }

    fun generateAllGames(topPartHeight: Double): HTMLDivElement {
        val statList = listOf("Oynanan Oyun Sayısı","Kazanılan Oyun Sayısı", "Galibiyet Serisi", "En Uzun Seri","Doğru Tahmin Oranı")
        return document.create.div {
            id = "allGameStats"
            style = """
                margin-top: ${toPx(topPartHeight*0.5)};
                font-family: openSans, sans-serif;
                font-size: ${toPx(topPartHeight * 0.3)};
                width: 100%;
                position: absolute;
                left: 0px;
                bottom: 0px;
                
            """.trimIndent()
        }.also {



            it.append(addButton(topPartHeight , "Bu Sözcükle Meydan Oku"){
                Constants.gamePage.challengePressed()
            })
            it.append(addButton(topPartHeight , "Yeni Kelime"){
                Constants.gamePage.resetPlayfield()
                returnButtonFunction()
            })


            it.append{
                div {
                    style = """
                        height: ${toPx(topPartHeight * 0.2)};
                    """.trimIndent()
                }
            }

            it.append{
                div {
                    style = """
                        border-top: solid;
                        height: ${toPx(topPartHeight * 0.2)};
                    """.trimIndent()
                }
            }

            it.append{
                for( i in statList.indices){
                    div{
                        style = """
                        float: top;
                        margin-left: 5%;
                        margin-right: 5%;
                        height: ${toPx(topPartHeight * 0.6)};
                        display: flex;
                    """.trimIndent()
                    }.also {
                        it.append{
                            div{ //content
                                style = """
                                float: left;
                                
                            """
                                text(statList[i])
                            }

                            div{ // dots
                                style = """
                                border-bottom: 3px dotted black;      
                                flex-grow: 1;
                                height: ${toPx(topPartHeight * 0.33)};
                                box-sizing: border-box;
                            """.trimIndent()
                            }

                            div{ // number
                                id = "allstat_$i"
                                style = """
                                float: right;
                            """
                                text("more text")
                            }
                        }
                    }
                }
            }

            it.append(addButton(topPartHeight , "İstatistikleri Paylaş"){
                //Constants.gamePage.onGameReset()
                //returnButtonFunction()
                shareStatisticsFunction()
            })

            it.append{
                div {
                    style = """
                        height: ${toPx(topPartHeight * 0.3)};
                    """.trimIndent()
                }
            }
        }
    }

    fun updateStats(game: Game, dictionaryLine: String=""){

        document.getElementById("allstat_0")!!.textContent = game.stats.played.toString()
        document.getElementById("allstat_1")!!.textContent = game.stats.win.toString()
        document.getElementById("allstat_2")!!.textContent = game.stats.streak.toString()
        document.getElementById("allstat_3")!!.textContent = game.stats.maxStreak.toString()
        document.getElementById("allstat_4")!!.textContent = "%"+(game.stats.win.toDouble()/game.stats.played*100.0).toInt().toString()

        if(game.lastGameData.isValid){
            val lastGameWon = game.lastGameData.attempts==-1


            (document.getElementById("lastGameStats") as HTMLElement).hidden  =false
            document.getElementById("lastWord")!!.textContent = game.lastGameData.wordUppercase
            document.getElementById("lastTimer")!!.textContent = game.lastGameData.time
            document.getElementById("lastResult")!!.textContent = if(lastGameWon){
                "Son oyununda sözcüğü bilemedin."
            }else{
                "Son oyununda sözcüğü ${game.lastGameData.attempts} denemede bildin."
            }
            document.getElementById("lastResultHead")!!.textContent = if(lastGameWon){
                "Üzgünüz..."
            }else{
                "YÜRÜ BE!"
            }
            if(dictionaryLine!=""){
                document.getElementById("lastDict")!!.textContent = "TDK Anlamı: " + dictionaryLine
            }

        }

    }



    fun generateTopPart(topPartHeight: Double): HTMLDivElement {
        return document.create.div{ // top row
            id= "statsTopRow"
            style ="""
                            width: 100%;
                            height: ${toPx(topPartHeight)};                     
                        """
        }.also {
            it.append {
                div{
                    id="titleDiv"
                    style =
                        """
                                        height:  ${toPx(topPartHeight)};
                                        text-align: left;
                                        margin-left: 5%;
                                        float: left;
                                        font-family: guessFont, sans-serif;
                                        font-size: ${toPx(topPartHeight*0.7)};
                                        text-align: center;
                                        line-height: ${toPx(topPartHeight)};
                                    """

                    text("İstatistikler")
                }

                div{
                    id="titleDiv"
                    style =
                        """
                                float: right;
                                height:  ${toPx(topPartHeight)};
                                width:  ${toPx(topPartHeight)};
                                text-align: center;
                                margin-top: auto;
                                """
                }.also {
                    it.append{
                        val extra = if(document.URL.contains("localhost")){
                            ""
                        }else{
                            "/sozck"
                        }

                        button{
                            onClickFunction = {event->
                                returnButtonFunction()
                            }
                            style = """
                                            opacity: 1;
                                            width:  ${toPx(topPartHeight * 0.8)};
                                            border: none;
                                            height: ${toPx(topPartHeight * 0.8)};
                                            margin-top: ${toPx(topPartHeight * 0.1)};
                                            background: transparent;
                                            background-image: url("${extra}/icon/cross.png");
                                            background-repeat: no-repeat;
                                            background-size: ${toPx(topPartHeight * 0.5)} ${toPx(topPartHeight * 0.5)};
                                            background-position: center;
                                        """
                        }.also {
                            it.className = "iconClass"
                        }
                    }
                }
            }
        }
    }



}